/* ================================================================================== */
/* Quran */
.memder{
    position: relative;
    text-align: center;
    overflow: hidden;
    box-shadow:-2px -2px 5px  #00ccff,
				2px 2px 5px #d400d4;
                height: 17vh;
}

.member-caption{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 1; 
    -webkit-transform: scale(1,1); 
            transform: scale(1,1);
    transition: all 1s;
}

.memder:hover img{
    -webkit-transform: scale(1.2,1.2);
            transform: scale(1.2,1.2);
}
.member-caption h2{
    color: #fff;
}

.QuranS{
    cursor: pointer;
}

