/* ==================================================================================== */
/* pray Time */
.boxPrayerTimes{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0);
    overflow: hidden;
    width: 200px;
    height: 20vh;
    text-align: center;
}

.boxPrayerTimes::before{
    content: '';
    position: absolute;
    background: linear-gradient(#00ccff,#d400d4);
    width: 100px;
    height: 150%;
    -webkit-animation: animate 4s linear infinite;
            animation: animate 4s linear infinite;
}

.boxPrayerTimes::after{
    content: '';
    position: absolute;
    background-color: rgb(0, 0, 0);
   inset: 4px;
}


@-webkit-keyframes animate{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


@keyframes animate{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.boxPrayerTimes div{
    position: relative;
    z-index: 10;
    font-size: 2rem;
}

#BtnMore{
    margin: 20px auto 50px;
}

@media screen and (max-width:575px){
	
	.container-PrayerTimes{
	margin: auto;
    width: 50%;
	}
}
